import React, { Component } from 'react';
import axios from 'axios';

import { Button, Form, Input, InputGroupAddon, InputGroupText, InputGroup, Container, Row, Col } from 'reactstrap';

class ContactForm extends Component {
  handleSubmit(e) {
    e.preventDefault();
    const name = document.getElementById('name').value;
    const email = document.getElementById('email').value;
    const message = document.getElementById('message').value;
    axios({
      method: 'POST',
      url: '/api/send',
      data: {
        name: name,
        email: email,
        message: message,
      },
    }).then((response) => {
      if (response.data.msg === 'success') {
        alert('Message Sent.');
        this.resetForm();
      } else if (response.data.msg === 'fail') {
        alert('Message failed to send.');
      }
    });
  }

  resetForm() {
    document.getElementById('contact-form').reset();
  }

  render() {
    return (
      <div className="section landing-section" id="readme">
        <Container>
          <Row>
            <Col className="ml-auto mr-auto" md="8">
              <h2 className="text-center">ESCRÍBEME!</h2>
              <Form id="contact-form" onSubmit={this.handleSubmit.bind(this)} className="contact-form" method="POST">
                <Row>
                  <Col md="6">
                    <label htmlFor="name">Nombre</label>
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <img src="https://img.icons8.com/offices/20/000000/user-group-man-man.png" alt="group" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input placeholder="Nombre" type="text" name="name" id="name" />
                    </InputGroup>
                  </Col>
                  <Col md="6">
                    <label htmlFor="exampleInputEmail1">Email</label>
                    <InputGroup>
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <img src="https://img.icons8.com/ultraviolet/20/000000/new-post.png" alt="post" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input placeholder="Email" type="email" name="email" id="email" aria-describedby="emailHelp" />
                    </InputGroup>
                  </Col>
                </Row>
                <label htmlFor="message">Mensaje</label>
                <Input placeholder="Mensaje" type="textarea" rows="4" id="message" name="message" />
                <Row>
                  <Col className="ml-auto mr-auto" md="4">
                    <Button className="btn-fill" color="warning" size="lg" type="submit">
                      Send Message
                    </Button>
                  </Col>
                </Row>
              </Form>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default ContactForm;
